/**
 * Custom CSS
 */

 span, p, label, div {
  font-family: 'Source Sans Pro', Arial, sans-serif;
}

button, a, a:before, a:after {
  font-family: 'Source Sans Pro', Arial, sans-serif;
  border: 0;
  outline: 0;
  transition: all 0.2s ease-in-out;
}


 h1 {
   font-family: 'Monstrat', Helvetica, sans-serif;
   font-weight: 700;
   line-height: 1.25em;
   color: #fff;
   text-transform: uppercase;
   font-size: 2.25rem;
   text-shadow: 0 0 20px rgb(255 255 255 / 60%);
   letter-spacing: 0.1em;
 }

 h2, h3, h4, h5, h6 {
   font-family: 'Monstrat', Helvetica, sans-serif;
   font-weight: 700;
   line-height: 1.25em;
 }

/*
 *  VIDEO WRAPPER
 */
 .videoWrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
  width: 100%;
}

.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*
 *  SCROLL STYLES - https://codepen.io/GhostRider/pen/GHaFw
 */
/* Secondary */
#scrollstyle-secondary::-webkit-scrollbar-track {
	background-color: #EEE;
	border-radius: 10px;
}

#scrollstyle-secondary::-webkit-scrollbar {
	width: 6px;
	background-color: #EEE;
}

#scrollstyle-secondary::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background-image: -webkit-gradient(linear,
									   left top,
									   left bottom,
									   color-stop(0.44, #35c5f3),
									   color-stop(0.72, #35b0f3),
									   color-stop(0.86, #35a1f3));
}

